import Swiper from 'swiper';
import { Navigation, Pagination, EffectFade, Autoplay } from 'swiper/modules';

import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/effect-fade';

const slider = () => {
	const classNames = {
		slider: '.js-slider',
		wrapper: '.js-slider-w',
		arrowNext: '.js-next-btn',
		arrowPrev: '.js-prev-btn',
		pagination: '.js-slider-pagination',
	};

	const $sliderWrappers = document.querySelectorAll(classNames.wrapper);

	if (!$sliderWrappers.length) return;

	$sliderWrappers.forEach(($wrapper) => {
		const $slider = $wrapper;

		if (!$slider) return;

		const $prevArrow = $wrapper.querySelector(classNames.arrowPrev);
		const $nextArrow = $wrapper.querySelector(classNames.arrowNext);
		const $pagination = $wrapper.querySelector(classNames.pagination);

		const sliderInstance = new Swiper($slider, {
			modules: [Navigation, Pagination, EffectFade, Autoplay],
			effect: 'fade',
			fadeEffect: {
				crossFade: true,
			},
			autoplay: {
				disableOnInteraction: false,
			},
			speed: 3000,
			pagination: {
				el: $pagination,
				type: 'custom',
				renderCustom: (sliderInstance, current, total) => {
					// eslint-disable-next-line
					return ' <span>0' + current + ' </span>' + '<span>0' + total + '</span>';
				},
			},

			observer: true,
			observeParents: true,
			autoHeight: true,
			loop: true,
			navigation: {
				prevEl: $prevArrow,
				nextEl: $nextArrow,
			},
		});
	});
};

export default slider;
